import React from 'react';
import '../css/MusicInfo.css';
import MusicItem from './MusicItem';
import MusicDatabase from './MuiscDatabase';
import AlbumPortal from './AlbumPortal';
import TrackListPortal from './TrackListPortal';
import logo from '../media/logo.png';
import $ from 'jquery';





class MusicInfo extends React.Component {
  constructor(props) {
    super(props)
    this.state = { 
      music_list: [],
      current_music_items: [],
      current_album_info: [],
      current_search_info: [],
      favourites: [],
      favourites_music_list: [],
      current_genre: [],
      render_search: "false",
      skip_deactivate: "false",
      name: [],
      typingTimeout: 0,
      error_message: [],
      sync_play_audio: [],
    }
 
    this.myRef = React.createRef()
  }



  /* CHANGE MAIN STATES */
  /* Input Search Part1  */
  changeName(event) {
    /*( on render always start at top )*/
    this.myRef.current.scrollTo(0, 0);
    /*( empty input dont run function )*/
    var term = this.state.name;
    if (event.target.value === '') {
      this.setState({
        search_timeout_disable: true
      })
      this.searchPlayList(this.state.current_genre)
      return
    }

    /*( set state changes )*/
  else {
    this.setState({
      track_can_play: [],
      favourites_active: "false",
      album_ready: "false",
      change_component_active: "false",
      track_list_ready: "false",
      album_active: "false",
      render_search: "true",
      skip_deactivate: "true",
      error_message: [],
      search_timeout_disable: false
    })

    /*( display active state )*/
    $('.music_info_genre').removeClass('disable');
    $('.music_info_album').removeClass('enable');
    $('.music_info_tracklist').removeClass('enable');

    /*( call search render smoothly )*/
    const self = this;
    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }
    self.setState({
      music_list: [],
      name: event.target.value,
      typingTimeout: setTimeout(function () {
        if (term !== '') {
          self.searchMusic(self.state.name);
        }
      }, 2000)
    });
  
  }
     
  }


  /* Input Search Part2 */
  searchMusic(term) {
    /*( fetch )*/
    var newList = [];
    MusicDatabase.searchTitle(term).then(response => {
      /*( fetch - check for error )*/
      if (response.data.length === 0) {
        this.state.error_message = "nothing found";
        $('.music_info_genre').addClass('disable');
        $('.music_info_album').removeClass('enable');
        $('.music_info_tracklist').removeClass('enable');
      }
      /*( fetch - filter redundant data )*/
      response.data.map(value => {
        if (value.preview != "") {
          newList.push(value)
        }
      })
      /*( fetch - apoint new data - if search_timout is not disabled )*/
      if(this.state.search_timeout_disable === true && this.state.name.length < 2){
        return
      }
      else {
        this.setState({
          music_list: newList,
        })
      }
       

      /*( add more tracks to the render )*/
      this.renderMoreSearch()
    })
  }



  /* Change To PlayList */
  searchPlayList(id) {
    /*( on render always start at top )*/
    this.myRef.current.scrollTo(0, 0);
    /*( disable favourites state )*/
    if (this.state.favourites_active === "true" && this.state.favourites_music_list.length === 0) {
      this.setState({
        music_item_play: [],
        favourites: [],
        music_list: []
      })
      /*( hide audio )*/
      $(".audio_player").removeClass("active")
    }
    /*( empty search field )*/
    $('.input').val('');
    /*( set state changes )*/
    this.setState({
      track_can_play: [],
      skip_deactivate: "true",
      track_list_ready: "false",
      music_list: [],
      favourites_active: "false",
      render_search: "false",
      error_message: [],
    })

    /*( fetch )*/
    var newList = [];
    MusicDatabase.searchPlayList(id).then(response => {
      response.tracks.data.map(value => {
        if (value.preview != "" && value.readable != false) {
          newList.push(value)
        }

      })
      /*( apoint data )*/
      this.setState({
        music_list: newList,
        album_ready: "false",
        change_component_active: "false",
        current_genre: response.id
      })
    })
    /*( display active state )*/
    $('.music_info_genre').removeClass('disable');
    $('.music_info_album').removeClass('enable');
    $('.music_info_tracklist').removeClass('enable');
  }


  /* Change To Album */
  changeToAlbum(id) {
    /*( on render always start at top )*/
    this.myRef.current.scrollTo(0, 0);
    /*( empty search field )*/
    $('.input').val('');
    /*( set State changes )*/
    this.setState({
      track_can_play: [],
      music_list: [],
      track_list_ready: "false",
      render_search: "false",
      error_message: [],
      pause_tracklist_listener: "false",
    })
    /*( fetch )*/
    var album_list = [];
    MusicDatabase.searchAlbum(id).then(response => {
      /*( set State changes )*/
      this.setState({
        change_component_active: "true",
        skip_deactivate: "true",
        favourites_active: "false",
      })
      response.tracks.data.map(value => {
        if (value.preview != "" && value.readable != false) {
          album_list.push(value)
        }
      })
      /*( set State changes )*/
      this.setState({
        current_album_info: response,
        music_list: album_list,
        artist_id: response.artist.id
      })
      /*( smooth functioning )*/
      setTimeout(function () { }, 1000)
      this.setState({
        album_ready: "true",
      }, () => {
        /*( display active state )*/
        $('.music_info_genre').addClass('disable');
        $('.music_info_tracklist').removeClass('enable');
        $('.music_info_album').addClass('enable');
      })
    })
  }


  /* Change To Tracklist */
  changeToTrackList(name) {
    /*( on render always start at top )*/
    this.myRef.current.scrollTo(0, 0);
    /*( set State changes )*/
    this.setState({
      track_can_play: [],
      music_list: [],
      album_ready: "false",
      render_search: "false",
      error_message: [],
      pause_tracklist_listener: "false",
      playlist_return_data: name
    })
    /*( fetch )*/
    var artist_track_list = [];
    MusicDatabase.searchTitle(name).then(response => {
      /*( set State changes )*/
      this.setState({
        change_component_active: "true",
        skip_deactivate: "true",
        favourites_active: "false",
      })
      response.data.map(value => {
        if (value.preview != "" && value.readable != false) {
          artist_track_list.push(value)
        }
      })
      /*( set State changes )*/
      this.setState({
        music_list: artist_track_list,
        artist_track_list: response,
      })
      /*( set State changes )*/
      this.setState({
        track_list_ready: "true"
      }, () => {
        /*( display active state )*/
        $('.music_info_album').removeClass('enable');
        $('.music_info_tracklist').addClass('enable');
      })
    })
  }


  /* RENDER MORE BUTTONS */
  /* Render More Input Search */
  renderMoreSearch() {

    var more_music = this.state.music_list;
    /*( fetch)*/
    MusicDatabase.searchMoreTitles(this.state.name, this.state.music_list.length + 25).then(response => {
      response.data.map(value => {
        if (value.preview != "") {
          more_music.push(value)
        }
      });
    })
    /*( remove duplicate values)*/
    var single_values_arr = more_music;
    const uniqueList = Array.from(new Set(single_values_arr.map(a => a.id))).map(id => {
      return single_values_arr.find(a => a.id === id)
    })
    more_music = uniqueList
    /*( apoint new data )*/
    this.setState({
      music_list: more_music,
    })
  }

  /* Render More Playlist Tracks */
  moreTracksTrackList(name) {

    var artist_track_list = this.state.artist_track_list.data;
    /*( fetch )*/
    MusicDatabase.searchMoreTrackList(name, this.state.music_list.length + 25).then(response => {
      response.data.map(value => {
        if (value.preview != "") {
          artist_track_list.push(value)
        }
      })
      /*( remove duplicate values)*/
      var single_values_arr = artist_track_list;
      const uniqueList = Array.from(new Set(single_values_arr.map(a => a.id))).map(id => {
        return single_values_arr.find(a => a.id === id)
      })
      artist_track_list = uniqueList

      /*( apoint new data )*/
      this.setState({
        music_list: artist_track_list,
      })
    })
  }


  /* FAVOURITES */
  /* Change State To Favourites */
  changeToFavourites() {

    this.myRef.current.scrollTo(0, 0);
    /*( check for empty state )*/
    if (this.state.favourites_music_list.length < 0) {
      return
    }
    else {
      this.setState({
        music_list: [],
        skip_deactivate: "true",
        track_list_ready: "false",
        favourites_active: "true",
        error_message: [],
      })
      /*( empty search field )*/
      $('.input').val('');
      /*( set state changes )*/
      this.setState({
        music_list: this.state.favourites_music_list,
        album_ready: "false",
        change_component_active: "false",
      })
      /*( display active state )*/
      $('.music_info_genre').removeClass('disable');
      $('.music_info_album').removeClass('enable');
      $('.music_info_tracklist').removeClass('enable');
      /*( bug fix playback symbols did not sync ( possibly jquery causing error with DOM ) )*/
      if(this.state.check_play != "false"){
        var audioplayer = document.getElementById("audio");
        audioplayer.pause();
        audioplayer.play();
      }
    }
  }





  /* MUSIC PLAYBACK AND RELATED STATE CHANGES */
  /*  Play Track Defualt State */

  playSong(track, album, title, album_art, id) {
    /*( find exisitng playing tracks in the DOM and apply styling )*/
    if (track === this.state.sync_play_audio) {
      this.setState({
        check_play: "true",
        check_pause: "false",
      })
    }
    /*( find exisitng playing tracks in the DOM and apply styling )*/
    if (track !== this.state.sync_play_audio) {
      this.setState({
        check_play: "false",
        check_pause: "true",
      })
    }


    /*( play song )*/
    this.state.current_music_items.map(value => {
      if (value === track) {
        this.setState({
          track_can_play: [],
          music_item_play: track,
          music_item_title: title,
          music_item_album: album,
          music_item_album_art: album_art,
          music_id: id,
          skip_active: "false",
          change_component_active: "false",
          skip_deactivate: "false",
        })
      }
    })
    /*( audio player play )*/
    var audioplayer = document.getElementById("audio");
    audioplayer.play();
    $(".audio_player").addClass("active")
  }


  /*  Play Album Track */
  playAlbumTrack(value, album_art) {
    /*( find exisitng playing tracks in the DOM and apply styling )*/
    if (value.preview === this.state.sync_play_audio) {
      this.setState({
        check_play: "true",
        check_pause: "false",
      })
    }
    /*( find exisitng playing tracks in the DOM and apply styling )*/
    if (value.preview !== this.state.sync_play_audio) {
      this.setState({
        check_play: "false",
        check_pause: "true",
      })
    }
    /*( set State changes )*/
    this.setState({
      track_can_play: [],
      music_item_play: value.preview,
      music_id: value.id,
      music_item_title: value.title,
      music_item_album_art: album_art.cover_small,
      music_item_album: value.artist.name,
      change_component_active: "true",
      tracklist_track_active: 'false',
      skip_deactivate: "false",
    })
    /*( audio player play )*/
    var audioplayer = document.getElementById("audio");
    audioplayer.play();
    $(".audio_player").addClass("active")
  }

  /*  Play TrackList Track */
  playTrackListTrack(value) {
    /*( find exisitng playing tracks in the DOM and apply styling )*/
    if (value.preview === this.state.sync_play_audio) {
      this.setState({
        check_play: "true",
        check_pause: "false",
      })
    }
    /*( find exisitng playing tracks in the DOM and apply styling )*/
    if (value.preview !== this.state.sync_play_audio) {
      this.setState({
        check_play: "false",
        check_pause: "true",
      })
    }
    /*(set state changes)*/
    this.setState({
      track_can_play: [],
      music_item_play: value.preview,
      music_id: value.id,
      music_item_title: value.title,
      music_item_album: value.album.title,
      music_item_album_art: value.album.cover_small,
      change_component_active: "true",
      tracklist_track_active: 'true',
      skip_deactivate: "false",
    })
    /*( audio player play )*/
    var audioplayer = document.getElementById("audio");
    audioplayer.play();
    $(".audio_player").addClass("active")
  }



  /* On Can Play */
  onCanPlay() {
    /*( set State changes )*/
    this.setState({
      track_can_play: "true",
      check_play: "true",
      check_pause: "false",
      sync_play_audio: this.state.music_item_play,
    })
  }

  /*  Stadard On Play */
  onPlay() {
    /*( sync playback - DOM styling )*/
    if (this.state.music_item_play === this.state.sync_play_audio) {
      this.setState({
        check_play: "true",
        check_pause: "false",
      })
    }
  }

  /* Pause Function Onclick Music Item Box */
  pauseSong() {
    /*(audio player)*/
    var audioplayer = document.getElementById("audio");
    audioplayer.pause();
    /*( set state changes - incl plaback DOM sync)*/
    this.setState({
      track_can_play: [],
      check_play: "false",
      check_pause: "true",
      pause_tracklist_listener: "true",
    })
  }

  /* Standard On Pause (sync audio player button and active musiclist item button) */
  pauseSync(e) {
    this.setState({
      check_pause: "true",
      check_play: "false",
    })
  }


  /* Skip Forward Audio Player */
  skipForward() {
    /*( check for maximum skips limit )*/
    var maxSkip = this.state.music_list.length - 1;
    if (this.state.music_id === this.state.music_list[maxSkip].id) {
      this.setState({
        check_pause: "false",
        check_play: "true",
        pause_tracklist_listener: "false"
      })
      return
    }

    else {
      /*( set State - ensure DOM styling changes when track plays)*/
      this.setState({
        check_pause: "true",
        check_play: "false",
        skip_active: "false",
        pause_tracklist_listener: "false"
      })

      /* (findng a match) */
      var musicList = this.state.music_list;
      var currentItem = this.state.music_id;
      var currentIndex = [];
      musicList.map(value => {
        if (value.id === currentItem) {
          var index = musicList.indexOf(value);
          currentIndex.push(index);
        }
      })

      /* (format and add up) */
      var removeArr = currentIndex++;
      var nextIndex = removeArr + 1;
      var nextMusicItemInfo = [];

      musicList.map(value => {
        if (musicList.indexOf(value) === nextIndex) {
          nextMusicItemInfo.push(value)
        }
      })

      /*(tracklist skip state)*/
      if (this.state.change_component_active === "true" && this.state.tracklist_track_active === 'true') {
        this.setState({
          music_item_album_art: nextMusicItemInfo[0].album.cover_small,
          music_item_play: nextMusicItemInfo[0].preview,
          music_item_title: nextMusicItemInfo[0].title,
          music_item_album: nextMusicItemInfo[0].album.title,
          music_id: nextMusicItemInfo[0].id,
          skip_active: "true",

        })
      }

      /*(album skip state)*/
      else if (this.state.change_component_active === "true") {
        this.setState({
          music_item_play: nextMusicItemInfo[0].preview,
          music_item_title: nextMusicItemInfo[0].title,
          music_item_album: nextMusicItemInfo[0].artist.name,
          music_id: nextMusicItemInfo[0].id,
          skip_active: "true",
        })
      }

      /*(defualt state)*/
      else {
        this.setState({
          music_item_album_art: nextMusicItemInfo[0].album.cover_small,
          music_item_play: nextMusicItemInfo[0].preview,
          music_item_title: nextMusicItemInfo[0].title,
          music_item_album: nextMusicItemInfo[0].album.title,
          music_id: nextMusicItemInfo[0].id,
          skip_active: "true",
        })
      }
    }
  }


  /* Skip Backward Audio Player */
  skipBackward() {
    /*( check for maximum skips limit )*/
    if (this.state.music_id === this.state.music_list[0].id) {
      this.setState({
        check_pause: "false",
        check_play: "true",
        pause_tracklist_listener: "false"
      })
      return
    }
    /*( set State - ensure DOM styling changes when track plays)*/
    else {
      this.setState({
        check_pause: "true",
        check_play: "false",
        skip_active: "false",
        pause_tracklist_listener: "false"
      })
      var musicList = this.state.music_list;
      var currentItem = this.state.music_id;
      var currentIndex = [];
      /* (finding a match) */
      musicList.map(value => {
        if (value.id === currentItem) {
          var index = musicList.indexOf(value);
          currentIndex.push(index);
        }
      })
      /* (format and add up) */
      var removeArr = currentIndex++;
      var nextIndex = removeArr - 1;
      /* retreive the next track info */
      var nextMusicItemInfo = [];
      musicList.map(value => {
        if (musicList.indexOf(value) === nextIndex) {
          nextMusicItemInfo.push(value)
        }
      })

      /*(tracklist skip state)*/
      if (this.state.change_component_active === "true" && this.state.tracklist_track_active === 'true') {
        this.setState({
          music_item_album_art: nextMusicItemInfo[0].album.cover_small,
          music_item_play: nextMusicItemInfo[0].preview,
          music_item_title: nextMusicItemInfo[0].title,
          music_item_album: nextMusicItemInfo[0].album.title,
          music_id: nextMusicItemInfo[0].id,
          skip_active: "true",
        })
      }

      /*(album skip state)*/
      else if (this.state.change_component_active === "true") {
        this.setState({
          music_item_play: nextMusicItemInfo[0].preview,
          music_item_title: nextMusicItemInfo[0].title,
          music_item_album: nextMusicItemInfo[0].artist.name,
          music_id: nextMusicItemInfo[0].id,
          skip_active: "true",
        })
      }

      /*(defualt state)*/
      else {
        this.setState({
          music_item_album_art: nextMusicItemInfo[0].album.cover_small,
          music_item_play: nextMusicItemInfo[0].preview,
          music_item_title: nextMusicItemInfo[0].title,
          music_item_album: nextMusicItemInfo[0].album.title,
          music_id: nextMusicItemInfo[0].id,
          skip_active: "true",

        })
      }
    }
  }

  /* Reset Play/Pause */
  reset() {
    this.setState({
      skip_active: "false"
    })
  }

  /* Errors */
  onError() {
    this.setState({
      track_can_play: "false"
    })
  }






  /* ADD AND REMOVE ITEMS FAVOURITES */
  addFavourites(value) {

    var favourites = this.state.favourites;
    var favourites_music_list = this.state.favourites_music_list;
    var condition = [];
    var index = [];
    var favourites_active = this.state.favourites_active;
    /*()*/
    /*( first add )*/
    if (favourites.length === 0) {
      favourites.push(value.id);
      favourites_music_list.push(value);
      this.setState({
        favourites: favourites,
        favourites_music_list: favourites_music_list
      })
      return
    }

    /*( second + Add )*/
    this.state.favourites.map(val => {
      if (val === value.id) {
        condition = "false"
        index.push(favourites.indexOf(val))
      }
    })

    /*( filtering out duplicates )*/
    if (condition == "false") {
      var remove_item_array = favourites.filter(function (x) {
        return x !== value.id;
      });
      var remove_item_music_list = favourites_music_list.filter(function (x) {
        return x.id !== value.id;
      });
      this.setState({
        favourites: remove_item_array,
        favourites_music_list: remove_item_music_list,
      })
    }

    /*(add item to list)*/
    if (condition != "false") {
      favourites.push(value.id)
      favourites_music_list.push(value);
      this.setState({
        favourites: favourites,
        favourites_music_list: favourites_music_list
      })
    }

    /*( apoint new data )*/
    if (favourites_active === "true") {
      this.setState({
        music_list: remove_item_music_list
      })
    }

    /*( reset condition )*/
    condition = [];
  }


  /* AUDIO PLAYER STYLING FOR BROWSERS */
  /* determine browser */
  getBrowser() {
    if (navigator.userAgent.indexOf("Chrome") != -1) {
      $('.audio_player').css({ background: "hsl(200, 12%, 95%)" });
      $('.audio_skip').css({ color: "grey" });
      $('.audio_skip.hide').css({ color: "lightgrey" });
      $('.audio_player_titles .first').css({ color: "coral" });
      $('.audio_player_titles .second').css({ color: "grey" })
    } else if (navigator.userAgent.indexOf("Opera") != -1) {
      console.log("Opera");
    } else if (navigator.userAgent.indexOf("MSIE") != -1) {
      $('.audio_player').css({ background: "black" });
      $('.audio_skip').css({ color: "white" });
      $('.audio_skip.hide').css({ color: "lightgrey" });
      $('.audio_player_titles .first').css({ color: "lightgrey" });
      $('.audio_player_titles .second').css({ color: "white" });;
    } else if (navigator.userAgent.indexOf("Firefox") != -1) {
      $('.audio_player').css({ background: "hsl(0, 0%, 13%)" });
      $('.audio_skip').css({ color: "white" });
      $('.audio_skip.hide').css({ color: "lightgrey" });
      $('.audio_player_titles .first').css({ color: "lightgrey" });
      $('.audio_player_titles .second').css({ color: "white" });;
    }
    else if (navigator.userAgent.indexOf("Safari") != -1) {
      $('.audio_player').css({ background: "hsl(180, 1%, 24%)" });
      $('.audio_skip').css({ color: "white" });
      $('.audio_skip.hide').css({ color: "lightgrey" });
      $('.audio_player_titles .first').css({ color: "lightgrey" });
      $('.audio_player_titles .second').css({ color: "white" });
    }
  }




  /* RENDER */
  render() {

    /* DOM STATE CHANGE STYLING */


    /* look for browser (audio styling) */
    this.getBrowser()

    /* LOADER */

    /* loader */
    if (this.state.music_list.length > 0 || this.state.error_message === "nothing found") {
      $('.loader').addClass("disable")
    }
    else {
      $('.loader').removeClass("disable")
    }

    
    /* FAVOURITES */
    /* favourites list empty and favourites state active - return to previous home state */
    if (this.state.favourites_active === "true" && this.state.favourites_music_list.length === 0) {
      this.searchPlayList(this.state.current_genre)
      this.state.music_id = [];
    }

    /* favourites heart onclick styling */
    if (this.state.favourites.length > 0) {
      $('.genre_item_heart').addClass('active')
    }
    else {
      $('.genre_item_heart').removeClass('active')
    }

    /* MUSIC PLAYER */

    /* skip buttons active? */
    if (this.state.skip_deactivate === "true") {
      $('.audio_skip').addClass('hide')
    }
    else {
      $('.audio_skip').removeClass('hide')
    }

    /* INPUT SEARCH */

    /* search input error message */
    var error_message = [];

    if (this.state.error_message === "nothing found") {
      error_message.push(<p className="error_message">NOTHING FOUND...</p>)
    }
    else {
      error_message = []
    }

    /* render requested search results when ready */
    if (this.props.renderSearch === "true") {
      this.state.music_list = this.props.renderSearchResultsMusicList
    }

    /* RENDER MORE BUTTON */

    /* display render more button */
    if (this.state.render_search === "true" && this.state.favourites_active != "true") {
      $('.spacer_genre p').css({ display: "block" })
    }
    else {
      $('.spacer_genre p').css({ display: "none" })
    }



    /* PUSH AVAILABLE STATE CHANGE REQUESTS INTO DOM VAR'S */
    /* push available genre items into DOM var */
    var genre_items = [];
    if (typeof this.state.music_list != 'undefined' && this.state.change_component_active != "true") {
      if (this.state.music_list.length > 0) {
        this.state.music_list.map(value => {
          if (value.preview !== "" && value.readable !== false) {
            this.state.current_music_items.push(value.preview)
            genre_items.push(< MusicItem pauseSong={(e) => this.pauseSong(e)}
              playSong={(track, album, title, album_art, id) => this.playSong(track, album, title, album_art, id)}
              changeToAlbum={(id) => this.changeToAlbum(id)}
              info={value}
              key={value.id}
              selector={value.id}
              check_play={this.state.check_play}
              check_pause={this.state.check_pause}
              music_id={this.state.music_id}
              skip={this.state.skip}
              skip_active={this.state.skip_active}
              track_can_play={this.state.track_can_play}
              addFavourites={(val) => this.addFavourites(val)}
              favourite_log={this.state.favourites}
              favourites_active={this.state.favourites_active}
              sync_play_audio={this.state.sync_play_audio}
            />)
          }
        })
      }
    }


    /* push available album information into DOM var */
    var album_portal = [];
    if (this.state.album_ready === 'true') {
      album_portal.push(< AlbumPortal
        playTrack={(value, album_art) => this.playAlbumTrack(value, album_art)}
        pauseTrack={(e) => this.pauseSong(e)}
        key={this.state.current_album_info.id}
        album_info={this.state.current_album_info}
        checkPause={this.state.check_pause}
        checkPlay={this.state.check_play}
        music_id={this.state.music_id}
        changeToTrackList={(e) => this.changeToTrackList(e)}
        artist_name={this.state.current_album_info.artist.name}
        album_active={this.state.change_component_active}
        addFavourite={(val) => this.addFavourites(val)}
        favouriteLog={this.state.favourites}
        current_music_item={this.state.music_item_play}
        resetState={(e) => this.resetState(e)}
        skip_active={this.state.skip_active}
        pause_tracklist_listener={this.state.pause_tracklist_listener}
      />)
    }



    /* push available tracklist information into DOM var  */
    var track_list = [];
    if (this.state.track_list_ready === "true") {
      track_list.push(< TrackListPortal
        key={this.state.artist_id}
        playTrackListTrack={(value) => this.playTrackListTrack(value)}
        track_list_artist={this.state.music_list}
        track_list_info={this.state.artist_track_list}
        pauseTrack={(e) => this.pauseSong(e)}
        checkPause={this.state.check_pause}
        checkPlay={this.state.check_play}
        music_id={this.state.music_id}
        track_list_ready={this.state.track_list_ready}
        moreTracks={(e) => this.moreTracksTrackList(e)}
        addFavourite={(val) => this.addFavourites(val)}
        favouriteLog={this.state.favourites}
        changeToAlbum={(id,  back_option) => this.changeToAlbum(id,  back_option)}
        albumChangeBack={this.state.current_album_info}
        pause_tracklist_listener={this.state.pause_tracklist_listener}
        

      />)
    }


    return (
      <div className="main_wrapper">

        {/* top bar (dark) */}
        <div className="top_wrapper">
          <div className='top_wrapper_param'>
            <div className="logo"> <img src={logo}></img>  <h2>SirionCloud</h2> </div>
            <div className="search_bar"> <input className="input" onChange={(val) => this.changeName(val)} spellCheck="false" placeholder="Search" ></input> <i className="fa fa-bars"></i> </div>
          </div>
        </div>

        {/* bottom section */}
        <div className="bottom_wrapper">
          <div className="main_wrapper_container">
            <div className="main_wrapper">

              {/* genre bar  */}
              <div className="genre_bar">
                <div className='genre_bar_param'>
                  <div className="genre_item" onClick={(e) => this.searchPlayList(3220851222)} > Brazilian <div className="genre_bar_item_border"></div> </div>
                  <div className="genre_item" onClick={(e) => this.searchPlayList(1615514485)} > Jazz <div className="genre_bar_item_border"></div> </div>
                  <div className="genre_item" onClick={(e) => this.searchPlayList(735488796)} > Indie <div className="genre_bar_item_border"></div> </div>
                  <div className="genre_item" onClick={(e) => this.searchPlayList(1767932902)} > Blues <div className="genre_bar_item_border"></div> </div>
                  <div className="genre_item" onClick={(e) => this.searchPlayList(4485213484)} > Soul <div className="genre_bar_item_border"></div> </div>
                  <div className="genre_item" onClick={(e) => this.searchPlayList(2113355604)} > Dance <div className="genre_bar_item_border"></div> </div>
                  <div className="genre_item" onClick={(e) => this.searchPlayList(1283464975)} > Pop <div className="genre_bar_item_border"></div> </div>
                  <div className="genre_item" onClick={(e) => this.searchPlayList(3801761042)} > Electronic <div className="genre_bar_item_border"></div> </div>
                  <div className="genre_item_heart" onClick={(e) => this.changeToFavourites(e)} > <i className="fa fa-heart"></i> <div className="genre_bar_item_border"></div> </div>
                </div>
              </div>


              {/* information display area */}
              <div ref={this.myRef} className='music_info_container'>
                <div className='loader'>
                  <div className="example">
                    <div className="sk-wave">
                      <div className="sk-wave-rect"></div>
                      <div className="sk-wave-rect"></div>
                      <div className="sk-wave-rect"></div>
                      <div className="sk-wave-rect"></div>
                      <div className="sk-wave-rect"></div>
                    </div>
                  </div>
                </div>
                {/* error  */}
                {error_message}


                {/* render area for genre items */}
                <div className='music_info_genre'>
                  {genre_items}
                  <div className="spacer_genre"> <p onClick={(e) => this.renderMoreSearch(e)}>+</p>  </div>
                </div>

                {/* render area for album items  */}
                <div className='music_info_album'>
                  {album_portal}
                </div>

                {/* render area for tracklist items  */}
                <div className='music_info_tracklist'>
                  {track_list}
                </div>

              </div>

              {/* audio player */}
              <div className="audio_player">
                <div className="audio_info">
                  <img src={`${this.state.music_item_album_art}`}></img>
                  <div className="audio_player_titles"> <p className="first">{this.state.music_item_title}</p><p className="second" >{this.state.music_item_album}</p> </div>
                </div>
                <div className="audio_skip"><i onClick={(e) => this.skipBackward(e)} className="fa fa-backward"></i>
                  <i onClick={(e) => this.skipForward(e)} className="fa fa-forward"></i></div>

                <audio
                  onPause={(e) => this.pauseSync(e)}
                  id="audio"
                  className="audio"
                  onError={(e) => this.onError(e)}
                  onCanPlay={(e) => this.onCanPlay(e)}
                  onPlay={(e) => this.onPlay(e)}
                  controls
                  autoPlay
                  loop
                  src={this.state.music_item_play} >
                </audio>

              </div>
            </div>
          </div>
        </div>
      </div>

    );
  }

  /* SETTING DEFUALT STATE*/
  componentDidMount() {
    /* default music items home screen */
    var newList = [];
    if (this.state.music_list.length < 1) {
      MusicDatabase.searchPlayList(3220851222).then(response => {
        response.tracks.data.map(value => {
          if (value.preview != "" && value.readable != false) {
            newList.push(value)
          }
        })
        this.setState({
          skip_deactivate: "true",
          music_list: newList,
          current_genre: response.id
        })
      })
    }

  }


}



export default MusicInfo;



