import React from 'react';
import '../css/TrackListPortal.css';
import $ from 'jquery'
import { cadetblue } from 'color-name';



class TrackListPortal extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      state_ids: [],
      active_track: [],
      selector: this.props.music_id
    }
  }


  /*PLAYBACK*/
  /* Play Track */
  playTrack(value) {
    this.props.playTrackListTrack(value)
    $('.play_track_list').removeClass('disable');
    $('.pause_track_list').removeClass('active');
  }

  /* Pause Track */
  pauseTrack(value) {
    this.props.pauseTrack(value);
  }


  /* Add to Favourite */
  addFavourite(value) {
    this.props.addFavourite(value)
    this.setState({
      active_track: value.id
    })
    $(`.${value.id}`).toggleClass("light_up")
  }

  /* Change To Album Portal */
  changeToAlbum(value) {
    this.props.changeToAlbum(value.album.id, true)
  }

  changeBackToAlbum(value) {
    this.props.changeToAlbum(value.id)
  }




  /* Change To Artist Portal */

  moreTracks() {
    this.props.moreTracks(this.props.track_list_artist[0].artist.name)
  }


  /* Render */
  render() {


    /* Render Right Track Info */
    if (this.props.track_list_ready === "true") {
      var current_track = [];
      var track_list_art_right = <div className="track_list_art_right"> <img src={`${this.props.track_list_artist[0].artist.picture_big}`}></img> </div>;
      var track_list_title_right = <h5 className="title">--</h5>;
      var track_list_track_list_title_right = <h4>--</h4>;
      var track_list_playback_title_right = "--";
      this.props.track_list_artist.map(value => {
        if (value.id === this.props.music_id) {
          current_track.push(value)
          track_list_art_right = <div className="track_list_art_right"> <img src={`${current_track[0].album.cover_big}`}></img> </div>
          track_list_title_right = <h5 className="title">{current_track[0].title}</h5>
          track_list_track_list_title_right = <h4>{current_track[0].album.title}</h4>
          track_list_playback_title_right = current_track[0].title.toUpperCase();
        }
      })


      var play_pause_right_container = "NOW PLAYING ";
      if (this.props.checkPlay === "true") {
        play_pause_right_container = "NOW PLAYING "
      }
      else if (this.props.pause_tracklist_listener === "true") {
        play_pause_right_container = "PAUSED "
      }


      /* Render Track List List Items */
      if (this.props.track_list_ready === "true") {
        var track_list = [];
        this.props.track_list_artist.map(value => {
          var get_minutes = Math.floor(value.duration / 60);
          var sum = get_minutes * 60;
          var get_seconds = value.duration - sum;
          if (get_seconds.toString().length === 1) {
            get_seconds = "0" + get_seconds.toString();
          }
          this.state.state_ids.push(value.id)
          track_list.push(<div key={value.id} className="track_item_track_list">
            <div className="track_art" style={{ background: `url("${value.album.cover_medium}")`, backgroundSize: "contain", backgroundPosition: "center" }}></div>
            <div className="track_info_track_list">
              <div className="track_info_info">
                <h5 onClick={(e) => this.changeToAlbum(value)} id="artist">{value.album.title}</h5>
                <div><h4>{value.title}</h4>
                  <i onClick={(e) => this.addFavourite(value)} className={`${value.id} fa fa-heart`}></i>
                  <span className="sec_symbol_attr">&#x27F3; </span>
                  <span className="sec_attr">{get_minutes}:{get_seconds} </span>

                </div>
              </div>
              <div id={value.id} className="play_pause_track_list">
                <i onClick={(e) => this.playTrack(value)} className="fa fa-play play_track_list"></i>
                <i onClick={(e) => this.pauseTrack(value.preview)} className="fa fa-pause pause_track_list"></i>
              </div>
            </div>
          </div>)
        })
      }
    }


    /* Searching For Selected Favourites - applying correct styling */
    this.props.favouriteLog.map(value => {
      if (value === this.state.active_track) {
        $(`.${value}`).addClass("light_up")
        return
      }
    })



    /* Playback Styling Active Track */
    if (this.props.music_id) {
      $('.track_item_track_list').removeClass('active')
      $(`#${this.props.music_id}`).parent('.track_info_track_list').parent('.track_item_track_list').addClass('active')
    }

    if (this.props.skip_active == "true") {
      $('.play_track_list').removeClass('disable');
      $('.pause_track_list').removeClass('active');
    }

    if (this.props.checkPlay !== "false") {
      $('.play_track_list').removeClass('disable');
      $('.pause_track_list').removeClass('active');
      $(`#${this.props.music_id}`).children('.play_track_list').addClass('disable');
      $(`#${this.props.music_id}`).children('.pause_track_list').addClass('active');
    }

    else {
      $(`#${this.props.music_id}`).children('.play_track_list').removeClass('disable');
      $(`#${this.props.music_id}`).children('.pause_track_list').removeClass('active');
    }

    




    /* Return */
    return (
      <div className="track_list_container">
        {/* Track List Left Information */}
        <div className="track_list_inner_left">
          {/* Track List Left Information */}
          <div className="track_list_inner_left_heading">
            <div className="track_list_inner_left_heading_left"> <div className="track_list_art"
              style={{ background: `url("${this.props.track_list_artist[0].artist.picture_medium}")`, backgroundSize: "cover", backgroundPosition: "center" }} >
            </div>
            </div>
            <div className="track_list_inner_left_heading_right">
              <div className="track_list_inner_left_heading_right_top"> <h2>{this.props.track_list_artist[0].artist.name}</h2> <h1>Track List</h1></div>
              <div className="track_list_inner_left_heading_right_bottom">
                <div className=" track_list_inner_left_heading_right_bottom_top">
                  <i className="fa fa-music"> {this.props.track_list_info.total}</i>
                  <a href={`https://www.facebook.com/sharer/sharer.php?u=https://www.deezer.com/en/artist/${this.props.track_list_artist[0].artist.id}`}>Facebook</a>
                  <a href={`https://twitter.com/home?status=https://www.deezer.com/en/artist/${this.props.track_list_artist[0].artist.id} `}>Twitter</a>
                  <a href={`https://pinterest.com/pin/create/button/?url=https://www.deezer.com/en/artist/${this.props.track_list_artist[0].artist.id}&media=&description=`}>Pintrest</a>
                </div>
                <div onClick={(e) => this.changeBackToAlbum(this.props.albumChangeBack)} className=" track_list_inner_left_heading_right_bottom_bottom bottom_special_align">
                  <p>Return to {this.props.albumChangeBack.title}</p>
                </div>
              </div>
            </div>
          </div>

          {/* Track List Track List */}
          <div className="track_list_inner_left_list_container">
            <div className="track_list_inner_left_list">
              {track_list}
            </div>

            <div className="spacer">

              <p onClick={(e) => this.moreTracks(e)}>+</p>

            </div>
          </div>
        </div>

        {/* Track List Right Inforamtion */}
        <div className="track_list_inner_right">
          <div className="track_list_inner_right_information">
            {track_list_art_right}
            <div className="overlap_info">
              {track_list_title_right}
              {track_list_track_list_title_right}
              <div className="playback_status">
                <h5 id="title_right">{play_pause_right_container + track_list_playback_title_right}</h5>
              </div>

            </div>
          </div>
        </div>
      </div>

    );
  }

  /* Playback DOM Styling - search for active items */
  componentDidMount() {

    this.props.favouriteLog.map(value => {
      this.state.state_ids.map(value2 => {
        if (value === value2) {
          $(`.${value}`).addClass("light_up")
        }
      })
    })

    if (this.props.checkPlay !== "false") {
      $('.play_track_list').removeClass('disable');
      $('.pause_track_list').removeClass('active');
      $(`#${this.props.music_id}`).children('.play_track_list').addClass('disable');
      $(`#${this.props.music_id}`).children('.pause_track_list').addClass('active');
      $('.track_item_track_list').removeClass('active')
      $(`#${this.props.music_id}`).parent('.track_info_track_list').parent('.track_item_track_list').addClass('active')
    }
    else {
      $(`#${this.props.music_id}`).children('.play_track_list').removeClass('disable');
      $(`#${this.props.music_id}`).children('.pause_track_list').removeClass('active');
    }
  }
}



export default TrackListPortal;



