import React from 'react';
import '../css/MusicItem.css';
import $ from 'jquery'





class Musicitem extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      selector: this.props.selector,
      favourites: [],
    }
  }


  /* Play Song*/
  playSong() {
    var track = this.props.info.preview;
    var album = this.props.info.album.title;
    var title = this.props.info.title_short;
    var album_art = this.props.info.album.cover_small;
    var id = this.state.selector;
    this.props.playSong(track, album, title, album_art, id);
    $(".play_song").removeClass("control_change");
    $(".play_song").removeClass("active");
    $(`#${this.state.selector}`).addClass("control_change")
    $(`#${this.state.selector}`).addClass("active")
  }



  /* Pause Song*/
  pauseSong() {
    $(`#${this.state.selector}`).removeClass("control_change");
    this.props.pauseSong()
  }


  /* Add To Favourites*/
  addFavourites(val) {
    $(`.${this.props.selector}`).toggleClass("light_up");
    this.props.addFavourites(val)
    this.setState({
      favourites: val.id
    })
  }

  /*  Change To Album */
  changeToAlbum() {
    this.props.changeToAlbum(this.props.info.album.id)
  }



  /*  render*/
  render() {

    /* check for skip action - apply playback styling in DOM */
    if (this.props.skip_active === "true" && this.props.info.preview === this.props.sync_play_audio) {
      $(".play_song").removeClass("control_change");
      $(".play_song").removeClass("active");
      $(`#${this.props.music_id}`).addClass("active")
    }

    if (this.props.check_pause === "true") {
      if (this.state.selector === this.props.music_id) {
        $(`#${this.props.music_id}`).removeClass("control_change");
      }
    }
    if (this.props.check_play === "true") {
      if (this.state.selector === this.props.music_id) {
        $(`#${this.props.music_id}`).addClass("control_change");
      }
    }




    /* Render */
    return (
      
      <div className="item_container">
        <div className='item_wrapper'>

          {/* top item section */}
          <div className='section_top'>
            <div style={{ background: `url(${this.props.info.album.cover_big})`, backgroundSize: 'cover', backgroundPosition: "center" }} className="section_top_inner">
              <div id={this.props.selector} className="play_song">
                <i onClick={(e) => this.playSong(e)} className="fa fa-play play"></i>
                <i onClick={(e) => this.pauseSong(e)} className="fa fa-pause pause"></i>
              </div>
            </div>
          </div>

          {/* bottom item section */}
          <div className='section_bottom'>
            <div className='bottom_left'>
              <h4>{this.props.info.title}</h4>
              <h5 onClick={(e) => this.changeToAlbum(e)}>{this.props.info.album.title}</h5>
            </div>
            <div className='bottom_right'>
              <i className={`${this.props.selector} fa fa-heart`} onClick={(e) => this.addFavourites(this.props.info)} ></i>
            </div>
          </div>
        </div>
      </div>

    );
  }


/* applying styling to activated DOM elements ( shared tracks across states ) */
  componentDidMount() {
/*(playback styling)*/
    if (this.props.check_play === "true") {
      if (this.state.selector === this.props.music_id) {
        $(`#${this.state.selector}`).addClass("control_change");
        $(`#${this.state.selector}`).addClass("active")
      }
    }
/*(favourites selection styling)*/
    if (this.props.favourite_log.length > 0) {
      this.props.favourite_log.map(value => {
        if (value === this.props.selector) {
          $(`.${this.props.selector}`).addClass("light_up")
        }
      })
    }
  }

}






export default Musicitem;



