import React from 'react';
import '../css/AlbumPortal.css';
import $ from 'jquery'
import { validate } from '@babel/types';





class AlbumPortal extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      state_ids: [],
      active_track: []
    }
  }


  /*PLAYBACK*/
  /* Play Track */
  playTrack(value, album_art) {
    this.props.playTrack(value, album_art)
    this.setState({
      current_track: value.id,
      track_right_title: []
    })
    $('.play_album').removeClass('disable');
    $('.pause_album').removeClass('active');
  }

  /* Pause Track */
  pauseTrack(value) {
    this.props.pauseTrack();
  }

  /* Add to Favourite */
  addFavourite(value) {
    var new_value = value;
    new_value.album = { cover_big: this.props.album_info.cover_big, title: this.props.album_info.title, id: this.props.album_info.id };
    console.log(new_value)
    this.props.addFavourite(value)
    this.setState({
      active_track: value.id
    })
    $(`.${value.id}`).toggleClass("light_up")
  }


  /* Change To Artist Portal */
  changeToTrackList() {
    this.props.changeToTrackList(this.props.artist_name)
  }

  /* Reset */
  resetState() {
    this.props.resetState()
  }


  /* Render */
  render() {


    /* Render Album List Items */
    if (this.props.album_info != []) {
      var assets = this.props.album_info;
      var track_list = [];
      if (this.props.album_info.tracks.data) {
        this.props.album_info.tracks.data.map(value => {
          var get_minutes = Math.floor(value.duration / 60);
          var sum = get_minutes * 60;
          var get_seconds = value.duration - sum;
          if (get_seconds.toString().length === 1) {
            get_seconds = "0" + get_seconds.toString();
          }
          this.state.state_ids.push(value.id)
          track_list.push(<div key={value.id} className="track_item_album">
            <div className="track_art_album" style={{ background: `url(${assets.cover_medium})`, backgroundSize: "cover" }}></div>
            <div className="track_info">
              <div className="track_info_info">
                <h5 onClick={(e) => this.changeToTrackList(e)} id="artist">{assets.artist.name}</h5>
                <div>
                  <h4>{value.title}</h4>
                  <i onClick={(e) => this.addFavourite(value)} className={`${value.id} fa fa-heart`}></i>
                  <span className="sec_symbol_attr">&#x27F3; </span>
                  <span className="sec_attr">{get_minutes}:{get_seconds} </span>
                </div>
              </div>
              <div id={value.id} className="play_pause_album">
                <i onClick={(e) => this.playTrack(value, this.props.album_info)} className="fa fa-play play_album"></i>
                <i onClick={(e) => this.pauseTrack(value)} className="fa fa-pause pause_album"></i>
              </div>
            </div>
          </div>)
        })
      }
    }

    /* Setting DOM Data */
    var current_right_title = "--";
    this.props.album_info.tracks.data.map(value => {
      if (value.id === this.props.music_id) {
        current_right_title = value.title
      }
    })

    var play_pause_right_container = "NOW PLAYING ";
    if (this.props.checkPlay === "true") {
      play_pause_right_container = "NOW PLAYING "
    }
    else if (this.props.pause_tracklist_listener === "true") {
      play_pause_right_container = "PAUSED "
    }

    /* Searching For Selected Favourites - applying correct styling */
    this.props.favouriteLog.map(value => {
      if (value === this.state.active_track) {
        $(`.${value}`).addClass("light_up")
        return
      }
    })

    /* Playback Styling Active Track */
    if (this.props.music_id) {
      $('.track_item_album').removeClass('active')
      $(`#${this.props.music_id}`).parent('.track_info').parent('.track_item_album').addClass('active')
    }

    if (this.props.skip_active == "true") {
      $('.play_album').removeClass('disable');
      $('.pause_album').removeClass('active');
    }

    if (this.props.checkPlay !== "false") {
      $('.play_album').removeClass('disable');
      $('.pause_album').removeClass('active');
      $(`#${this.props.music_id}`).children('.play_album').addClass('disable');
      $(`#${this.props.music_id}`).children('.pause_album').addClass('active');
    }
    else {
      $(`#${this.props.music_id}`).children('.play_album').removeClass('disable');
      $(`#${this.props.music_id}`).children('.pause_album').removeClass('active');
    }






    /* Return */
    return (
      <div className="album_container">
        {/* Album Left Information */}
        <div className="album_inner_left">

          {/* Album Top Info Bar */}
          <div className="album_inner_left_heading">
            <div className="album_inner_left_heading_left"> <div className="album_art" style={{ background: `url("${assets.artist.picture_medium}")`, backgroundSize: "cover", backgroundPosition: "center" }}></div> </div>
            <div className="album_inner_left_heading_right">
              <div className="album_inner_left_heading_right_top"> <h2 onClick={(e) => this.changeToTrackList(e)} id="artist">{assets.artist.name}</h2>
                <h1>{assets.title}</h1>
              </div>
              <div className="album_inner_left_heading_right_bottom">
                <div className=" album_inner_left_heading_right_bottom_top">
                  <i className="fa fa-heart"> {assets.fans}</i>
                  <i className="fa fa-music"> {assets.nb_tracks}</i>
                  <a href={`https://www.facebook.com/sharer/sharer.php?u=${assets.link}`}>Facebook</a>
                  <a href={`https://twitter.com/home?status=${assets.link} `}>Twitter</a>
                  <a href={`https://pinterest.com/pin/create/button/?url=${assets.link}&media=&description=`}>Pintrest</a>
                </div>
                <div className=" album_inner_left_heading_right_bottom_bottom bottom_special_align"> <p>{assets.label}</p></div>
              </div>
            </div>
          </div>

          {/* Album Track List */}
          <div className="album_inner_left_list_container">
            <div className="album_inner_left_list">
              {track_list}
            </div>

            <div className="spacer"></div>
          </div>
        </div>

        {/* Album Right Inforamtion */}
        <div className="album_inner_right">
          <div className="album_inner_right_information">
            <div className="track_art_right" style={{ background: `url("${this.props.album_info.cover_big}")`, backgroundSize: "cover", backgroundPosition: "top" }} ></div>
            <div className="overlap_info">
              <h5 className="title">{this.props.album_info.artist.name}</h5>
              <h4>{this.props.album_info.title}</h4>
              <div className="playback_status" >
                <h5 id="title_right"> {play_pause_right_container + current_right_title.toLocaleUpperCase()}</h5>
              </div>

            </div>

          </div>
        </div>
      </div>


    );
  }


  /* Playback DOM Styling - search for active items */
  componentDidMount() {

    this.props.favouriteLog.map(value => {
      this.state.state_ids.map(value2 => {
        if (value === value2) {
          $(`.${value}`).addClass("light_up")
        }
      })
    })

    if (this.props.checkPlay !== "false") {
      $('.play_album').removeClass('disable');
      $('.pause_album').removeClass('active');
      $(`#${this.props.music_id}`).children('.play_album').addClass('disable');
      $(`#${this.props.music_id}`).children('.pause_album').addClass('active');
      $('.track_item_album').removeClass('active')
      $(`#${this.props.music_id}`).parent('.track_info').parent('.track_item_album').addClass('active')
    }
    else {
      $(`#${this.props.music_id}`).children('.play_album').removeClass('disable');
      $(`#${this.props.music_id}`).children('.pause_album').removeClass('active');
    }
  }
}



export default AlbumPortal;



